.logo {
  grid-area: LOGO;
  margin: 0.5rem;
  & div div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    container-type: inline-size;
    background-image: url(../../resources/ksq_logo.jpg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    & .logo-text {
      color: var(--color-orange);
      font-size: 15cqw;
      padding-bottom: 8cqw;
      background: transparent;
      animation: lightening 5s var(--elastic-timing-function) infinite;
      &:first-of-type {
        font-size: 100cqw;
        line-height: 80cqw;
        padding-bottom: 0;
      }
    }
  }
}
@keyframes lightening {
  0%,
  100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.8;
  }
}
