.quiz-result {
  width: 60vw;
  display: flex;
  justify-content: center;
  align-items: center;
  container-type: inline-size;
  color: var(--color-lapis-lazuli);
  margin: auto;
  & div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    & h3 {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 8cqw;
      font-weight: 600;
    }
    & p {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 8cqw;
      font-weight: 600;
      &:nth-of-type(2) {
        color: var(--color-aquamarine);
      }
      &:nth-of-type(3) {
        color: var(--color-red);
      }
    }
  }
}
