header {
  display: grid;
  grid-template-columns: clamp(150px, 50vw, 250px) clamp(90px, 25vw, 180px) clamp(
      90px,
      25vw,
      180px
    );
  grid-template-areas:
    "LOGO PLAY DATAS"
    "QUESTION QUESTION QUESTION";
  place-content: center;
}

@media screen and (min-width: 992px) {
  header {
    grid-template-columns:
      clamp(200px, 20vw, 250px) clamp(150px, 15vw, 180px) clamp(
        150px,
        15vw,
        180px
      )
      1fr;
    grid-template-areas: "LOGO PLAY DATAS QUESTION";
  }
}
