  --color-red: rgba(255, 80, 80, 0.8);
  --color-desert: rgba(236, 200, 174, 0.6);
  --color-orange: rgba(227, 100, 20, 0.8);
  --color-aquamarine: rgba(35, 240, 199, 0.8);
  --bounce-timing-function: linear(
