code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  padding: 0;
  margin: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  box-sizing: border-box;
  user-select: none;
  text-decoration: none;
  font-family: "Prompt", sans-serif;
  font-size: 20px;
}
body {
  min-width: 330px;
  max-width: 1300px;
  min-height: 100vh;
  margin: auto;
}
.bubble-border-design {
  background: radial-gradient(whitesmoke, var(--color-lapis-lazuli));
  box-shadow: 0 0 8px 3px var(--color-lapis-lazuli);
  border: var(--bubble-border-width) solid var(--color-lapis-lazuli);
  animation: bubble-border 8s linear infinite;
  & > div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: 95%;
    background: radial-gradient(whitesmoke, var(--color-aquamarine));
    box-shadow: 0 0 8px 3px var(--color-aquamarine);
    border: var(--bubble-border-width) solid var(--color-aquamarine);
    border-radius: var(--bubble-border-1) var(--bubble-border-2)
      var(--bubble-border-3) var(--bubble-border-4);
    animation: bubble-border 8s linear infinite 2s;
    & > * {
      width: 95%;
      height: 95%;
      background-color: whitesmoke;
      box-shadow: 0 0 8px 3px whitesmoke;
      border: var(--bubble-border-width) solid whitesmoke;
      border-radius: var(--bubble-border-1) var(--bubble-border-2)
        var(--bubble-border-3) var(--bubble-border-4);
      animation: bubble-border 8s linear infinite 4s;
    }
  }
}

@keyframes bubble-border {
  0%,
  100% {
    border-radius: var(--bubble-border-1) var(--bubble-border-2)
      var(--bubble-border-3) var(--bubble-border-4);
  }
  25% {
    border-radius: var(--bubble-border-4) var(--bubble-border-1)
      var(--bubble-border-2) var(--bubble-border-3);
  }
  50% {
    border-radius: var(--bubble-border-3) var(--bubble-border-4)
      var(--bubble-border-1) var(--bubble-border-2);
  }
  75% {
    border-radius: var(--bubble-border-2) var(--bubble-border-3)
      var(--bubble-border-4) var(--bubble-border-1);
  }
}
