.question-data {
  grid-area: DATAS;
  container-type: inline-size;
  color: var(--color-lapis-lazuli);
  margin: 0.5rem;
  & div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    & p {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 25cqw;
      font-weight: 600;
      &:nth-of-type(2) {
        color: var(--color-aquamarine);
      }
      &:nth-of-type(3) {
        color: var(--color-red);
      }
    }
  }
}
