.question {
  grid-area: QUESTION;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-lapis-lazuli);
  margin: 0.5rem;
  & div {
    display: flex;
    justify-content: center;
    align-items: center;
    & p {
      padding: 1rem;
      font-size: 0.9rem;
      font-weight: 600;
    }
  }
}

@media screen and (min-width: 576px) {
  .question div p {
    font-size: 1.25rem;
  }
}

@media screen and (min-width: 992px) {
  .question div p {
    font-size: 1.9rem;
  }
}
